import { IAbonementOffer, id } from "../../common/types/domain";
import { AbonementOfferShownTarget } from "../store/modal";
import { selectExternalTeacher } from "./external";
import { selectCurrentSubjectId, selectStudentAbonementShowcaseShown } from "./novigation";
import { StudentSelector } from "./types";

export const selectShowcaseAbonementOffers: StudentSelector<IAbonementOffer[]> = (state) => {
  const showcase = selectStudentAbonementShowcaseShown(state);
  switch (showcase) {
    case AbonementOfferShownTarget.teacher: {
      const teacherAbonementOffers = state.externalTeacher.abonementOffers;
      return teacherAbonementOffers;
    }
    case AbonementOfferShownTarget.subject: {
      const currentSubjectId = selectCurrentSubjectId(state);
      const { abonementOffers: allOffers } = selectExternalTeacher(state);

      const offersThatSatisfy = allOffers.filter((offer) => offer.subjects.includes(currentSubjectId));
      return offersThatSatisfy;
    }
    default: {
      return [];
    }
  }
};
